.card {
    background-color: $white;
    padding: 1.5rem 1rem;
    border-radius: 1rem;
    grid-row: 2/3;
    position: relative;
    z-index: 5;
}

.container-right {
    grid-column: 9/14;
    grid-row: 2/3;
    align-items: flex-start;
    display: grid;
    grid-template-columns: repeat(7,1fr);
    grid-template-rows: auto auto auto;
    height: 100%;
    gap: .5rem;
}

// select::-ms

.invisible {
    display: none !important;
}

.input-container {
    position: relative;
    width: 95%;
    // background-color: rgb(58, 102, 184);
    text-align: left;
    padding: 1.5rem 5rem 1.5rem .5rem;

    input,
    label {
        display: block;
        width: 100%;
    }

    label {
        font-size: 2.75rem;
        font-weight: 700;
    }

    input {
        font-size: 2rem;
        padding: 1.5rem .25rem 1.5rem 2.5rem;
        margin-left: 2rem;
        background-color: $white;
        border: 0;
        box-shadow: 0 .5rem 1.5rem rgba($black, .275);
        border-radius: .25rem;
    }

    .eye-icon {
        position: absolute;
        height: 5rem;
        bottom: 15%;
        right: 6rem;
        font-size: 3rem;
        color: $blue-accent;
        cursor: pointer;
    }
}

.social-container {
    // background-color: aquamarine;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin: 4rem 0;
    background-color: rgba(244, 249, 255, 0.568);
    box-shadow: inset 0 0 3rem rgba($black, .2);
    max-height: 75%;

    // h1 {
    //     @include heading;
    //     margin-bottom: 3rem;
    // }

    .social-btn-container {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-grow: 5;

        .social-facebook,
        .social-google {
            position: relative;
            box-shadow: 0 -.15rem 3rem rgba($black, .2);
            border-radius: .25rem;
            font-size: 2.25rem;
            padding: 1.75rem 5.5rem 1.75rem 1.5rem;
            margin: 3rem 0;
            cursor: pointer;
            width: 90%;

            span {
                color: $white;

                &:hover {
                    text-decoration: none;
                }
            }
        }

        .social-google {
            background-color: #1A73E8;
            background-color: #E57373;
        }

        .social-facebook {
            background-color: #3C5A99;
        }

        // div {

        //     &:first-child {
        //         margin-left: 2rem;
        //     }

        //     &:last-child {
        //         margin-right: 2rem;
        //     }
        // }
    }
}


.brand-icon {
    position: absolute;
    top: 50%;
    right: 5%;
    transform: translate(0, -50%);
    font-size: 3.5rem;
    margin-left: .5rem;
    color: $white;
}

.flex-between {
    display: flex;
    justify-content: space-between;
}